import React, { useState, useEffect } from "react";
import MenuBar from "../components/MenuBar";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import { useCallback } from "react";
import { useNavigate } from "react-router-dom";

import { useNumberContext } from '../components/NumberContext';

export default function InputNumber() {
    const [numberValue, setNumberValue] = useState('');
    const [price, setPrice] = useState(0);
    const [buttonSubmitt, setButtonSubmitt] = useState(false);
    const multiplier = 2000;

    const calc = () => {
        setPrice(numberValue * multiplier);
    };

    useEffect(() => {
        // S dng useEffect  m bo rng price c cp nht sau khi numberValue thay i
        calc();
    }, [numberValue]);

    const handleNumberChange = (event) => {
        const inputValue = event.target.value;
  
        // Ch cho php gi li cc k t s
        const numericValue = inputValue.replace(/[^0-9]/g, '');
        setNumberValue(numericValue);
    };

    const navigate = useNavigate();
    
    const { updateNumbers } = useNumberContext();

    const handleSubmit = (event) => {
        // X l gi tr s  y
        updateNumbers(numberValue, price);
        setButtonSubmitt(true);
        event.preventDefault();
        console.log('Submitted number:', numberValue);
    };

    const confirmClick = useCallback(() => {
        navigate("/method");
    }, [navigate]);

  return (
    <div>
        <MenuBar/>
        <Box
        display="flex"
        flexDirection={"column"}
        justifyContent="center"
        alignItems="center"
        minHeight="98vh"
        sx={{
          bgcolor: "#F4F8FF",
        }}
        >
            <Box
                display="flex"
                flexDirection={"column"}
                justifyContent="flex-start"
                alignItems="center"
                sx={{
                bgcolor: "#FFFFFF",
                borderRadius: 10,
                border: '1px solid #1488DB',
                width: 650,
                height: 600
                }}
            >
                <Typography
                sx={{
                    color: "#1B3764",
                    fontSize: 36,
                    paddingTop: 5,
                    fontWeight: "bold",
                }}
                >
                Nhp s lng giy
                </Typography>
                <form onSubmit={handleSubmit}>
                    <Box
                    display="flex"
                    flexDirection={"column"}
                    justifyContent="center"
                    alignItems="center"
                    sx={{
                    mt: 5,
                    bgcolor: "#FFFFFF",
                    }}
                    >
                        <TextField
                        label=""
                        variant="outlined"
                        type="text" // S dng type="text"  hin th keyboard s trn in thoi
                        value={numberValue}
                        onChange={handleNumberChange}
                        sx={{
                        width: 400,
                        '& input': {
                            fontSize: '24px', // t kch thc ch s trong input
                            padding: '20px', // t l v  dy ca input
                            textAlign: 'center'
                        },
                        }}
                        />
                        <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        onClick={handleSubmit}
                        disabled={price===0}
                        sx={{
                        mt: 5,
                        fontSize: 16
                        }}
                        >
                        Xc nhn
                        </Button>
                    </Box>
                </form>
                <Box
                display="flex"
                flexDirection={"row"}
                justifyContent="space-between"
                alignItems="center"
                sx={{
                width: 400,
                bgcolor: "#FFFFFF",
                }}
                >
                    <Box
                    display="flex"
                    flexDirection={"column"}
                    justifyContent="center"
                    alignItems="flex-start"
                    sx={{
                    bgcolor: "#FFFFFF",
                    }}
                    >
                    <Typography
                    sx={{
                        color: "#1B3764",
                        fontSize: 28,
                        paddingTop: 5,
                        fontWeight: "bold",
                    }}
                    >
                    n gi:
                    </Typography>
                    <Typography
                    sx={{
                        color: "#1B3764",
                        fontSize: 28,
                        paddingTop: 2,
                        fontWeight: "bold",
                    }}
                    >
                    Tng:
                    </Typography>
                    </Box>
                    <Box
                    display="flex"
                    flexDirection={"column"}
                    justifyContent="center"
                    alignItems="flex-end"
                    sx={{
                    bgcolor: "#FFFFFF",
                    }}
                    >
                    <Typography
                    sx={{
                        color: "#1B3764",
                        fontSize: 28,
                        paddingTop: 5,
                        fontWeight: "bold",
                    }}
                    >
                    {multiplier}
                    </Typography>
                    <Typography
                    sx={{
                        color: "#1B3764",
                        fontSize: 28,
                        paddingTop: 2,
                        fontWeight: "bold",
                    }}
                    >
                    {price}
                    </Typography>
                    </Box>
                </Box>
                <Button
                variant="contained"
                color="primary"
                onClick={confirmClick}
                disabled={!buttonSubmitt || price===0}
                sx={{
                mt: 5,
                fontSize: 16
                }}
                >
                Tip tc
                </Button>
            </Box>
        </Box>
    </div>
    );
}