import React from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
} from "@mui/material";
import { usePrinterListContext } from "../components/PrinterListContext";

const EnableDialog = ({ printer, isOpen, handleClose, handleEnable }) => {
  const { editPrinter } = usePrinterListContext();
  const handleEnableConfirm = (confirmed) => {
    // X l khi xc nhn Enable hoc hy b
    handleClose();

    if (confirmed) {
      const updatedPrinter = { ...printer, status: "Sn sng" };
      editPrinter(updatedPrinter);
      handleClose();
      // Gi hm xc nhn enable t component cha
      // handleEnable();
    }
  };

  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle sx={{ backgroundColor: "green", color: "white" }}>
        Confirm
      </DialogTitle>
      <DialogContent sx={{ paddingBottom: "0px" }}>
        <Typography sx={{ padding: "30px" }}>
          Bn chc chn mun Enable my in?
        </Typography>
      </DialogContent>
      <DialogActions
        sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}
      >
        <Button
          onClick={handleClose}
          color="success"
          variant="outlined"
          sx={{ width: "120px" }}
        >
          Hy
        </Button>
        <Button
          onClick={() => handleEnableConfirm(true)}
          color="success"
          variant="contained"
          sx={{ width: "120px" }}
        >
          Chc chn
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default EnableDialog;
