// PrinterListContext.js
import React, { createContext, useContext, useState, useEffect } from "react";

const PrinterListContext = createContext();

export const usePrinterListContext = () => useContext(PrinterListContext);

const PrinterListProvider = ({ children }) => {
  const [printers, setPrinters] = useState(() => {
    const storedPrinters = localStorage.getItem("printers");
    return storedPrinters
      ? JSON.parse(storedPrinters)
      : [
          {
            id: 1,
            name: "Brother HL",
            location: "D An",
            building: "A2",
            room: " 101",
            status: "Sn sng",
          },
          {
            id: 2,
            name: "Canon PIXMA",
            location: "D An",
            building: "B3",
            room: " 102",
            status: "Khng sn sng",
          },
          {
            id: 3,
            name: "Dell E525W",
            location: "L Thng Kit",
            building: "A2",
            room: " 103",
            status: "Khng sn sng",
          },
          {
            id: 4,
            name: "Samsung Xpress",
            location: "D An",
            building: "A2",
            room: " 104",
            status: "Khng sn sng",
          },
          {
            id: 5,
            name: "Xerox Phaser",
            location: "L Thng Kit",
            building: "H6",
            room: " 105",
            status: "Sn sng",
          },
          {
            id: 6,
            name: "Lexmark  CX",
            location: "D An",
            building: "B3",
            room: " 106",
            status: "Khng sn sng",
          },
          {
            id: 7,
            name: "Ricoh SP ",
            location: "L Thng Kit",
            building: "A2",
            room: " 107",
            status: "Sn sng",
          },
          {
            id: 8,
            name: "Kyocera Ecosys",
            location: "D An",
            building: "C4",
            room: " 108",
            status: "Khng sn sng",
          },
          {
            id: 9,
            name: "HP LaserJet",
            location: "L Thng Kit",
            building: "H1",
            room: " 109",
            status: "Sn sng",
          },
          {
            id: 10,
            name: "Epson EcoTank",
            location: "D An",
            building: "H3",
            room: " 110",
            status: "Khng sn sng",
          },
        ];
  });

  useEffect(() => {
    localStorage.setItem("printers", JSON.stringify(printers));
  }, [printers]);
  const addPrinter = (newPrinter) => {
    setPrinters((prevPrinters) => {
      const updatedPrinters = [...prevPrinters, newPrinter];
      localStorage.setItem("printers", JSON.stringify(updatedPrinters));
      return updatedPrinters;
    });
  };

  const removePrinter = (printerToRemove) => {
    setPrinters((prevPrinters) =>
      prevPrinters.filter((printer) => printer.id !== printerToRemove.id)
    );
    updateLocalStorage();
  };

  const editPrinter = (updatedPrinter) => {
    setPrinters((prevPrinters) =>
      prevPrinters.map((printer) =>
        printer.id === updatedPrinter.id ? updatedPrinter : printer
      )
    );

    // Cp nht thng tin trong localStorage
    updateLocalStorage();
  };
  const updateLocalStorage = () => {
    localStorage.setItem("printers", JSON.stringify(printers));
  };
  const resetLocalStorage = () => {
    localStorage.removeItem("printers");
    setPrinters([
      {
        id: 1,
        name: "Brother HL",
        location: "D An",
        building: "A2",
        room: " 101",
        status: "Sn sng",
      },
      {
        id: 2,
        name: "Canon PIXMA",
        location: "D An",
        building: "B3",
        room: " 102",
        status: "Khng sn sng",
      },
      {
        id: 3,
        name: "Dell E525W",
        location: "L Thng Kit",
        building: "A2",
        room: " 103",
        status: "Khng sn sng",
      },
      {
        id: 4,
        name: "Samsung Xpress",
        location: "D An",
        building: "A2",
        room: " 104",
        status: "Khng sn sng",
      },
      {
        id: 5,
        name: "Xerox Phaser",
        location: "L Thng Kit",
        building: "H6",
        room: " 105",
        status: "Sn sng",
      },
      {
        id: 6,
        name: "Lexmark  CX",
        location: "D An",
        building: "B3",
        room: " 106",
        status: "Khng sn sng",
      },
      {
        id: 7,
        name: "Ricoh SP ",
        location: "L Thng Kit",
        building: "A2",
        room: " 107",
        status: "Sn sng",
      },
      {
        id: 8,
        name: "Kyocera Ecosys",
        location: "D An",
        building: "C4",
        room: " 108",
        status: "Khng sn sng",
      },
      {
        id: 9,
        name: "HP LaserJet",
        location: "L Thng Kit",
        building: "H1",
        room: " 109",
        status: "Sn sng",
      },
      {
        id: 10,
        name: "Epson EcoTank",
        location: "D An",
        building: "H3",
        room: " 110",
        status: "Khng sn sng",
      },
    ]);
  };
  return (
    <PrinterListContext.Provider
      value={{
        printers,
        addPrinter,
        removePrinter,
        editPrinter,
        setPrinters,
        resetLocalStorage,
      }}
    >
      {children}
    </PrinterListContext.Provider>
  );
};

export default PrinterListProvider;
