// PrinterDialog.js
import React, { useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  Grid,
  Box,
} from "@mui/material";
import UpdateDialog from "../components/UpdateDialog";
import DisableDialog from "../components/DisableDialog";
import EnableDialog from "../components/EnableDialog";
import {
  usePrinterListContext,
  PrinterListProvider,
} from "../components/PrinterListContext";

const sharedButtonStyle = {
  backgroundColor: "your-custom-color",
  boxShadow: 1,
  borderRadius: "5px",
  paddingTop: "2px",
  paddingBottom: "2px",
  marginRight: "10px",
  fontSize: "12px",
  // Thm cc thuc tnh khc theo nhu cu ca bn
};

const Detailprinter = ({ printer, isOpen, handleClose }) => {
  const { printers, editPrinter } = usePrinterListContext();
  const [updateDialogOpen, setUpdateDialogOpen] = useState(false);
  const [disableDialogOpen, setDisableDialogOpen] = useState(false);
  const [enableDialogOpen, setEnableDialogOpen] = useState(false);
  const handleCloseDetailprinter = () => {
    // X l cc thay i cn thit khi ng chi tit my in
    console.log("Chi tit my in  c ng");

    // ng chi tit my in
    handleClose();
  };

  const handleEnableConfirm = (confirmed) => {
    // X l khi xc nhn Enable hoc hy b
    setEnableDialogOpen(false);

    if (confirmed) {
      // Thc hin cp nht trng thi my in thnh Online
      const updatedPrinterInfo = { ...printer, status: "Sn sng" };
      editPrinter(updatedPrinterInfo);
    }
  };

  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle sx={{ textAlign: "center" }}>THNG TIN MY IN</DialogTitle>
      <DialogContent>
        {printer && (
          <Box>
            <Grid container xs={{ padding: "20px" }}>
              <Grid item md={6}>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100%",
                  }}
                >
                  <img
                    src="https://cdn.iconscout.com/icon/free/png-256/free-photocopier-machine-1125087.png"
                    style={{
                      width: "100%",
                      height: "100%",
                      objectFit: "cover",
                    }}
                  />
                </Box>
              </Grid>

              <Grid item md={6}>
                <Typography variant="h6">{printer.name}</Typography>
                <Typography color="text.secondary">ID: {printer.id}</Typography>
                <Typography color="text.secondary">
                  Mu my in: {printer.brand}
                </Typography>
                <Typography color="text.secondary">
                  C s: {printer.location}
                </Typography>
                <Typography color="text.secondary">
                  Ta: {printer.building}
                </Typography>
                <Typography color="text.secondary">
                  Phng: {printer.room}
                </Typography>
                {printer.status !== null && printer.status !== undefined && (
                  <Typography color="text.secondary">
                    Trng thi: {printer.status}
                  </Typography>
                )}
              </Grid>
            </Grid>
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                padding: "10px",
              }}
            >
              <Button
                onClick={() => setUpdateDialogOpen(true)}
                color="primary"
                variant="contained"
                sx={sharedButtonStyle}
              >
                Cp nht
              </Button>
              <Box>
                <Button
                  onClick={() => {
                    console.log("Printer:", printer);
                    console.log("Enable clicked");
                    setDisableDialogOpen(true);
                  }}
                  color="error"
                  variant="contained"
                  sx={sharedButtonStyle}
                  disabled={printer.status === "Khng sn sng"}
                >
                  Disable
                </Button>
                <Button
                  onClick={() => {
                    setEnableDialogOpen(true);
                  }}
                  color="success"
                  variant="contained"
                  sx={sharedButtonStyle}
                  disabled={printer.status === "Sn sng"}
                >
                  Enable
                </Button>
              </Box>
            </Box>
          </Box>
        )}
      </DialogContent>
      <UpdateDialog
        printer={printer}
        isOpen={updateDialogOpen}
        handleClose={() => {
          setUpdateDialogOpen(false);
          handleCloseDetailprinter(); // Gi hm ng t Detailprinter khi ng UpdateDialog
        }}
      />

      {/* Dialog v hiu ha */}
      <DisableDialog
        printer={printer}
        isOpen={disableDialogOpen}
        handleClose={() => {
          setDisableDialogOpen(false);
          handleCloseDetailprinter(); // Gi hm ng t Detailprinter khi ng UpdateDialog
        }}
      />

      {/* Dialog kch hot */}
      <EnableDialog
        printer={printer}
        isOpen={enableDialogOpen}
        handleEnable={handleEnableConfirm}
        handleClose={() => {
          setEnableDialogOpen(false);
          handleCloseDetailprinter(); // Gi hm ng t Detailprinter khi ng UpdateDialog
        }}
      />
    </Dialog>
  );
};

export default Detailprinter;
