import React from "react";
import { useNavigate } from "react-router-dom";
import MenuBar from "../components/MenuBar";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import { usePrintContext } from "../components/PrintContext";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";

export default function Bill() {
  const { printingInfo } = usePrintContext();
  const currentDate = new Date();

  const navigate = useNavigate();
  const gridItemStyle = {
    borderBottom: "1px solid #000000",
    padding: "2px",
  };

  return (
    <div>
      <MenuBar />
      <Box display="flex" justifyContent="center" alignItems="center">
        <Box
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          sx={{
            marginTop: 5,
          }}
        >
          <CheckCircleIcon sx={{ color: "#2cc31e", fontSize: 60 }} />
          <Typography
            sx={{
              color: "#2cc31e",
              fontFamily: "Be Vietnam Pro-Regular, Helvetica",
              fontSize: "48px",
              fontWeight: "bold",
              marginBottom: "5px",
            }}
          >
            In thnh cng
          </Typography>
          <Typography
            sx={{
              color: "#1b3764",
              fontFamily: "Be Vietnam Pro-Regular, Helvetica",
              fontSize: "16px",
              fontWeight: "bold",
              marginBottom: "5px",
            }}
          >
            Cm n bn  s dng dch v, bn c th xem li thng tin in 
            di y
          </Typography>
          <img
            alt="linh tinh"
            src="https://i.imgur.com/oBIHZkc.png"
            style={{ height: 2, width: 400, marginTop: 5 }}
          />
          <Typography
            sx={{
              color: "#1b3764",
              fontFamily: "Be Vietnam Pro-Regular, Helvetica",
              fontSize: "24px",
              marginBottom: "5px",
              marginTop: 3,
            }}
          >
            M In: {printingInfo.printID}
          </Typography>
          <Grid container spacing={2} sx={{ width: 600, marginTop: 2 }}>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                Tn tp in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >
                {printingInfo.fileName}
              </Typography>
            </Grid>

            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                Kiu in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >{`${printingInfo.orientation} ${
                printingInfo.doubleSided ? "2 mt" : "1 mt"
              }`}</Typography>
            </Grid>

            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                Ngy in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >
                {printingInfo.printDate}
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                S t in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >
                {printingInfo.printPages} t {printingInfo.paperSize}
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                Ni in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >
                {printingInfo.printerLocation}
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                ID My in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >
                {printingInfo.printerId}
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography variant="h6" sx={{ color: "#1b3764" }}>
                Tn my in:
              </Typography>
            </Grid>
            <Grid item xs={6} style={gridItemStyle}>
              <Typography
                variant="h6"
                textAlign="right"
                sx={{ color: "#1b3764" }}
              >
                {printingInfo.printerName}
              </Typography>
            </Grid>
          </Grid>
          <Button
            variant="contained"
            className="continue-button"
            color="info"
            onClick={() => navigate("/Home")}
            sx={{ width: "150px", height: "50px", marginTop: 3 }}
          >
            V trang ch
          </Button>
        </Box>
      </Box>
    </div>
  );
}
