import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Box,
  Grid,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  Typography,
} from "@mui/material";

const AddPrinterDialog = ({ isOpen, handleClose, handleAddPrinter }) => {
  const [newPrinter, setNewPrinter] = useState({
    name: "",
    brand: "",
    location: "",
    building: "",
    room: "",
    status: "Khng sn sng",
  });

  const [selectedLocation, setSelectedLocation] = useState("");

  const handleInputChange = (field, value) => {
    setNewPrinter({ ...newPrinter, [field]: value });
  };

  const handleAddClick = () => {
    if (
      newPrinter.name &&
      selectedLocation &&
      newPrinter.building &&
      newPrinter.room
    ) {
      const printerToAdd = {
        ...newPrinter,
        id: generatePrinterId(),
        location: selectedLocation,
      };

      // Thm my in mi vo danh sch trong context v cp nht localStorage
      handleAddPrinter(printerToAdd);

      // Reset cc trng v ng dialog
      setNewPrinter({
        name: "",
        brand: "",
        building: "",
        room: "",
        status: "Khng sn sng",
      });
      setSelectedLocation("");
      handleClose();
    } else {
      alert("Vui lng in y  thng tin trc khi thm my in.");
    }
  };

  const handleChange = (event) => {
    setSelectedLocation(event.target.value);
  };

  // Hm sinh id cho my in mi
  const generatePrinterId = () => {
    // Ly danh sch my in t localStorage
    const printers = JSON.parse(localStorage.getItem("printers")) || [];

    // Nu danh sch my in khng rng, ly id ca my in cui cng v tng gi tr ln 1
    const newId =
      printers.length > 0 ? printers[printers.length - 1].id + 1 : 1;

    return newId;
  };

  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle
        sx={{
          textAlign: "center",
          paddingTop: "60px",
          fontSize: "20px",
          fontWeight: "600",
          color: "#1B3764",
        }}
      >
        THM MY IN
      </DialogTitle>
      <DialogContent sx={{ padding: "10px 35px" }}>
        <Box sx={{ padding: "15px" }}>
          <Box>
            <Typography
              sx={{ color: "#666", fontSize: "16px", fontWeight: "600" }}
            >
              Tn my in
            </Typography>
            <TextField
              sx={{ marginBottom: "15px" }}
              id="outlined-helperText"
              fullWidth
              placeholder="Type here"
              size="small"
              value={newPrinter.name}
              onChange={(e) => handleInputChange("name", e.target.value)}
              InputProps={{
                sx: {
                  fontSize: "14px",
                },
              }}
            />
          </Box>
          <Grid container spacing={4}>
            <Grid item xs={6}>
              <Box>
                <Typography
                  sx={{ color: "#666", fontSize: "16px", fontWeight: "600" }}
                >
                  C s
                </Typography>
                <FormControl fullWidth>
                  <Select
                    id="demo-simple-select"
                    value={selectedLocation}
                    onChange={handleChange}
                    size="small"
                    sx={{
                      fontSize: "14px",
                    }}
                  >
                    <MenuItem value={"L Thng Kit"}>
                      C s L Thng Kit
                    </MenuItem>
                    <MenuItem value={"D An"}>C s D An</MenuItem>
                  </Select>
                </FormControl>
              </Box>
            </Grid>
            <Grid item xs={3}>
              <Box>
                <Typography
                  sx={{ color: "#666", fontSize: "16px", fontWeight: "600" }}
                >
                  Ta
                </Typography>
                <TextField
                  sx={{ marginBottom: "15px" }}
                  id="outlined-helperText"
                  fullWidth
                  placeholder="Type here"
                  size="small"
                  value={newPrinter.building}
                  onChange={(e) =>
                    handleInputChange("building", e.target.value)
                  }
                  InputProps={{
                    sx: {
                      fontSize: "14px",
                    },
                  }}
                />
              </Box>
            </Grid>
            <Grid item xs={3}>
              <Box>
                <Typography
                  sx={{ color: "#666", fontSize: "16px", fontWeight: "600" }}
                >
                  Phng
                </Typography>
                <TextField
                  sx={{ marginBottom: "15px" }}
                  id="outlined-helperText"
                  fullWidth
                  placeholder="Type here"
                  size="small"
                  value={newPrinter.room}
                  onChange={(e) => handleInputChange("room", e.target.value)}
                  InputProps={{
                    sx: {
                      fontSize: "14px",
                    },
                  }}
                />
              </Box>
            </Grid>
          </Grid>
        </Box>
      </DialogContent>
      <DialogActions sx={{ padding: " 20px 50px 65px 50px" }}>
        <Button onClick={handleClose} color="primary" variant="outlined">
          Cancel
        </Button>
        <Button onClick={handleAddClick} color="primary" variant="contained">
          Thm
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddPrinterDialog;
